import React, { Component } from "react";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";

import { 
	selectSystemCategory, 
	clearResults,
	updateResults,
	clearActiveMenuSelections,
	selectProductResultsData } from "../actions/index";
import SystemCategorySelector from "../components/SystemCategorySelector";
import Refinements from "../containers/Refinements";
import Results from "../components/Results";
import '../css/navigation.min.css';

class Navigation extends Component {
	constructor({props}) {
		super(props);

		let currentCategorySelected = null;

		this.categorySelectionListener = this.categorySelectionListener.bind(this);
		this.setupRefinementOptions = this.setupRefinementOptions.bind(this);
		this.getNextResults = this.getNextResults.bind(this);
	}

	componentDidMount() {}

	getNextResults(page) {
		this.props.updateResults( page );
	}

	categorySelectionListener(event) {
		const categorySelected = event.target.id;
		this.setupRefinementOptions(categorySelected);
	}

	setupRefinementOptions(categorySelected) {	
		switch( categorySelected ) {
			case "throughPenetrations":
			this.categorySelectedData = this.props.systemCategoryData[0].throughPenetrations;				
			break;

			case "joints": 
			this.categorySelectedData = this.props.systemCategoryData[0].joints;	
			break;

			case "wraps": 
			this.categorySelectedData = this.props.systemCategoryData[0].wraps;	
			break;

			default:
			this.categorySelectedData = null;
			break;
		}

		this.props.selectSystemCategory(this.categorySelectedData);
		this.props.clearResults();
		this.props.clearActiveMenuSelections();

		this.currentCategorySelected = categorySelected;

		const productResultsData = {
			"category": categorySelected,
			"data": this.props.systemCategoryData[0].results
		};

		this.props.selectProductResultsData(productResultsData);
	}

	render() {
		return (
			<div className="main-navigation-container">
				<SystemCategorySelector 
					handleCategorySelectionClick = { this.categorySelectionListener } 
					currentCategorySelected= { this.currentCategorySelected }
				/>
				<Refinements
					categorySelectedData={ this.categorySelectedData } 
					resultsData={this.props.productResultsData}
				/>
				<Results 
					resultsData={ this.props.productResultsData } 
					getNextResults={ this.getNextResults }
				/>
			</div>
		)
	}
}

function mapStateToProps(state) {
	//console.log(state);
	return { 
		systemCategoryData: state.systemCategoryData,
		productResultsData: state.productResultsData
	};
}

function matchDispatchToProps(dispatch) {
	return bindActionCreators({ 
		selectSystemCategory: selectSystemCategory,
		clearResults: clearResults,
		selectProductResultsData: selectProductResultsData,
		updateResults: updateResults,
		clearActiveMenuSelections: clearActiveMenuSelections 
	}, dispatch)
}

export default connect( mapStateToProps, matchDispatchToProps )( Navigation ); 
  


