import React from 'react';
import SelectionResult from "../components/SelectionResult";
//import '../css/secondary-category-menu-btn.min.css';

const SelectionList = ({ selections, cancelSelection, clearAllResults }) => {
    let selectionData = null;
    let clearResultsCss = "clear-results-btn";

    this.handleSelectionCancelation = function(primaryId, secondaryId) {
        cancelSelection( primaryId, secondaryId );
    }

    if(selections.length > 0) {
        clearResultsCss = "clear-results-btn active"
        selectionData = selections.map(( result, index ) => {
            return(
                <SelectionResult                    
                    key={index} 
                    primaryId={ result.primarySelectionId }
                    primaryLabel={ result.primarySelectionLabel }
                    secondaryId={ result.secondarySelectionId }
                    secondaryLabel={ result.secondarySelectionLabel }
                    onClick={ () => this.handleSelectionCancelation(result.primaryselectionId, result.secondarySelectionId) }
                />    
            );
        });
    }
    
    return( <div className="selection-result-container">
        {selectionData}
        <div 
            className={clearResultsCss}
            onClick={clearAllResults}
        >Clear results</div> 
    </div>);
}; 

export default SelectionList; 


