import React from 'react';
//import '../css/result.min.css';

const Result = ({ 
    id,
    description,
    documents,
    imageLink,
    productLink,
    partNumber,
    tRating,
    productOptions 
}) => {
    return(
        <div className="result-container">
            <div className="result-header">{partNumber}</div>
            <div className="result-image-container"><img src={imageLink} alt="product drawing" /></div>
            <div className="result-info-container">
                <span className="description-text">{description}</span>
                <span>Related documents: {documents.length}</span>
                <span>T Rating: {tRating}</span>
                <span>3M Product options: {productOptions}</span>
                <div className="result-button"><span>Download PDF</span></div>
                <div className="result-button"><span>View products</span></div>
            </div>
        </div>
    );
}; 

export default Result; 


