import React from 'react';
import '../css/selection-result.min.css';

const SelectionResult = ({ primaryId, primaryLabel, secondaryId, secondaryLabel, onClick }) => {
    return( <div className="selection-result">        
        
        <div 
            className="cancel-btn-container"
            onClick={ onClick }
        >
            <div className="cancel-btn">
                <img src="https://multimedia.3m.com/mws/media/1445033O/close-button.png" alt="close button icon" />
            </div>
        </div>
        
            
        
        <span className="primary-selection">{primaryLabel}: {secondaryLabel}</span>
    </div>);
}; 

export default SelectionResult; 


