export default function ( state=[], action ) {
    switch( action.type ) {
        case "UPDATE_BREADCRUMB":
        if(state) {
            return [
                ...state,
                {
                    primaryselectionId: action.payload.primaryselectionId,
                    primarySelectionLabel: action.payload.primarySelectionLabel,
                    secondarySelectionId: action.payload.secondarySelectionId,
                    secondarySelectionLabel: action.payload.secondarySelectionLabel,
                }
            ]
        }
        break;

        case "REMOVE_SELECTION":
        state.map((item, index ) => {
            if( item.primaryselectionId === action.payload ) {
                state.splice(index, 1);
            }
        }); 
        return [...state];

        case "CLEAR_RESULTS":
        return [];

        default:
        return state;
    }
}


