import React from 'react';

import Result from "../components/Result";
import '../css/results.min.css';

const Results = ({ resultsData, getNextResults }) => {
	let resultList = null;
	let nextResultClass = "next-results-button";
	let currentPage = 1;
	let numPages = 0;
	let numResultsToShow = 5;
	let results = null;

	this.handleGetNextResultsClick = function(){
		getNextResults(currentPage);		
	}

	//console.log("!!!!!!!!!!");
	//console.log( resultsData );
	
	if(resultsData.filteredData) {
		results = [...resultsData.filteredData];
		numPages = Math.ceil( resultsData.filteredData.length / numResultsToShow );
	}else {
		if( resultsData.data ) {
			results = [...resultsData.data];
			numPages = Math.ceil( resultsData.data.length / numResultsToShow );
		}
	}

	if( results ) {
		if(resultsData.currentPage) {
			numResultsToShow = numResultsToShow * resultsData.currentPage;
		} else {
			if(results.length < 5) {
				numResultsToShow = results.length;
			} else {
				numResultsToShow = 5;	
			}
		}

		nextResultClass = (resultsData.data.length > 5 && numResultsToShow <= resultsData.data.length ) ? "next-results-button visible" : "next-results-button";

		resultList = results.slice(0,numResultsToShow).map(( result, index ) => {
			return(
				<Result 
					key={result.id}
					id={result.id}
					description={result.description}
					documents={result.documents}
					imageLink={result.imageLink}
					productLink={result.productLink}
					partNumber={result.partNumber}
					tRating={result.tRating}
					productOptions={result.productOptions}
				/>
			);
		});
	}

    return( 
		<div className="results" >
			{ resultList }
			<div 
				className={nextResultClass}
				onClick={ this.handleGetNextResultsClick }>
				Get next results
			</div>
		</div>	
    );
}; 

export default Results; 


