export default function ( state=null, action ) {
    switch( action.type ) {
        case "PRIMARY_MENU_SELECTED":
        return action.payload;

        case "CLEAR_ACTIVE_MENU_SELECTIONS":        
        return action.payload;        
        
        default:
        return state;
    }
}


