import { combineReducers } from "redux";
import systemCategoryData from "./system-category-reducer";
import activeSystemCategory from "./active-system-category-reducer";
import activePrimaryMenuSelection from "./active-primary-menu-selection-reducer";
import activeSecondaryMenuSelection from "./active-secondary-menu-selection-reducer";
import breadcrumbSelections from "./selection-breadcrumb-reducer";
import productResultsData from "./product-results-reducer";

const allReducers = combineReducers({ 
    systemCategoryData: systemCategoryData,
    activeSystemCategory: activeSystemCategory,
    activePrimaryMenuSelection: activePrimaryMenuSelection,
    activeSecondaryMenuSelection: activeSecondaryMenuSelection,
    breadcrumbSelections: breadcrumbSelections,
    productResultsData: productResultsData
});

export default allReducers;


