export default function ( state=[], action ) {
    switch( action.type ) {

        case "SELECT_RESULTS_DATA":
        return action.payload;

        case "UPDATE_RESULTS_DATA":        
        let currentPage = action.payload;
        currentPage = currentPage + 1;
        state.currentPage = currentPage;
        return state;

        case "UPDATE_RESULTS_WITH_FILTERS":
        const filter = [action.payload.primaryselectionId, action.payload.secondarySelectionId];
        const filteredData = [];

        for(var i = 0; i < state.data.length; i++ ) {
            for( var j = 0; j < state.data[i].filters.length; j++ ) {
                for( var k = 0; k < state.data[i].filters[j].data.length; k++ ) {
                    if(state.category === state.data[i].filters[j].systemCategory) {
                        if(JSON.stringify(filter) === JSON.stringify(state.data[i].filters[j].data[k])) {                  
                            if(state.filteredData) {
                                state.filteredData.push(state.data[i]);
                            } else {
                                state.filteredData = [state.data[i]];
                            }
                        }
                    }
                }
            }
        }

        if(state.filteredData) {
            var uniq = new Set(state.filteredData.map( e => JSON.stringify(e)));
            var res = Array.from(uniq).map(e => JSON.parse(e));            
            state.filteredData = res;
        }

        return state;

        case "UPDATE_RESULTS_FROM_CANCELATION":
        const cancelationFilter = [action.payload.primaryId, action.payload.secondaryId ];
        state.filteredData = [...state.data];
        if(state.filteredData) {
            for( var l = 0; l < state.filteredData.length; l++ ) {            
                for( var m = 0; m < state.filteredData[l].filters.length; m++ ) {
                    for( var n = 0; n < state.filteredData[l].filters[m].data.length; n++ ) {
                        if(JSON.stringify( cancelationFilter ) === JSON.stringify( state.filteredData[l].filters[m].data[n] )) {
                            console.log("**");
                            console.log(cancelationFilter + " - " + state.filteredData[l].filters[m].data[n]);
                            console.log(state.filteredData[l]);
                            state.filteredData[l].remove = true;
                        }
                    }
                }
            }
        }

        state.filteredData.map((item, index) => {
            if(item.remove) {
                state.filteredData.splice(index, 1);
            }
        });
        return state;

        default:
        return state;
    }
}




