import React from 'react';
import '../css/primary-category-menu-btn.min.css';

const PrimaryCategoryMenuBtn = ({ id, btnLabel, data, onClick, className }) => {
    return( 
        <div onClick={ onClick } className={className}>
            <div >
                 <span>{btnLabel}</span>                  
            </div>
        </div>
    );
}; 

export default PrimaryCategoryMenuBtn; 


