import React from 'react';
//import '../css/secondary-category-menu-btn.min.css';

const SecondaryCategoryMenuBtn = ({ label, onClick }) => {
    return( 
        <div 
            className="secondary-category-menu-btn"
            onClick={onClick}
        >
        <span>{ label }</span>
    </div>);
}; 

export default SecondaryCategoryMenuBtn; 


