import React from 'react';
import SecondaryCategoryMenuBtn from "./SecondaryCategoryMenuBtn";
import '../css/system-category-secondary-menu.min.css';

const SystemCategorySecondaryMenu = ({ 
    activeSystemCategory,
    activePrimaryMenuSelection, 
    passSecondaryMenuBtnClickToRefinementContainer }) => {

    let secondaryMenuData = null;

    this.handleSecondaryCategoryMenuBtnClick = function(btnLabel, id) {
        passSecondaryMenuBtnClickToRefinementContainer(btnLabel, id);
    }

    var css = "system-category-secondary-menu";
    
    if(activeSystemCategory && activePrimaryMenuSelection ) {
        activeSystemCategory.map((btnData, index) => {
            css =  btnData.showSecondaryData ? "system-category-secondary-menu" : "system-category-secondary-menu hide";
            if( activePrimaryMenuSelection.id === btnData.id ) {                
                secondaryMenuData = btnData.data.map(( menu, index ) => {
                    return(
                        <div key={ index } className={ css }>
                            <SecondaryCategoryMenuBtn 
                                key={menu.id}
                                className="secondary-category-menu-btn"
                                label={menu.btnText}
                                onClick={ () => this.handleSecondaryCategoryMenuBtnClick(menu.btnText, menu.id)}
                            />
                        </div>
                    );
                });
            }
        });
    }

    return(<div className="system-category-secondary-menu-container">
        { secondaryMenuData }
    </div>);
}; 

export default SystemCategorySecondaryMenu;  


