export const selectSystemCategory = (categoryData) => {
    return {
        type: "SYSTEM_CATEGORY_SELECTED",
        payload: categoryData
    };
}; 

export const selectPrimaryMenuItem = (obj) => {
    return {
        type: "PRIMARY_MENU_SELECTED",
        payload: obj
    };
};

export const updateMenu = (obj) => {
    return {
        type: "UPDATE_MENU",
        payload: obj
    };
}; 

export const selectSecondaryMenuItem = (obj) => {
    return {
        type: "SECONDARY_MENU_SELECTED",
        payload: obj
    };
}; 

export const updateSecondaryMenu = (obj) => {
    return {
        type: "UPDATE_SECONDARY_MENU",
        payload: obj
    };
}; 

export const updateBreadcrumbWithSelection = (obj) => {
    return {
        type: "UPDATE_BREADCRUMB",
        payload: obj
    };
};

export const clearResults = () => {
    return {
        type: "CLEAR_RESULTS",
        payload: ""
    };
};

export const cancelSelection = (obj) => {
    return {
        type: "CANCEL_SELECTION",
        payload: obj
    };
};

export const removeSelectionFromList = (id) => {
    return {
        type: "REMOVE_SELECTION",
        payload: id
    };
};

export const selectProductResultsData = (obj) => {
    return {
        type: "SELECT_RESULTS_DATA",
        payload: obj
    };
};

export const updateResults = (page) => {
    return {
        type: "UPDATE_RESULTS_DATA",
        payload: page
    };
};

export const updateResultsWithFilters = (obj) => {
    return {
        type: "UPDATE_RESULTS_WITH_FILTERS",
        payload: obj
    };
};

export const clearActiveMenuSelections = () => {
    return {
        type: "CLEAR_ACTIVE_MENU_SELECTIONS",
        payload: ""
    };
};

export const updateResultsFromCancelation = (obj) => {
    return {
        type: "UPDATE_RESULTS_FROM_CANCELATION",
        payload: obj
    };
};

export const cancelAllSelections = () => {
    return {
        type: "CANCEL_ALL_SELECTIONS",
        payload: ""
    };
};






