import React from 'react';
import '../css/system-category-selector.min.css';

const SystemCategorySelector = ({handleCategorySelectionClick, currentCategorySelected }) => {
    
    let throughPenetrationCss = "transparent-overlay";
    let jointsCss = "transparent-overlay";
    let wrapsCss = "transparent-overlay";
    let preloaderCss = currentCategorySelected ? "preloader" : "preloader active"; 

    switch(currentCategorySelected) {
        case "throughPenetrations":
        throughPenetrationCss = "transparent-overlay selected";
        break;
        case "joints":
        jointsCss = "transparent-overlay selected";
        break;
        case "wraps":
        wrapsCss = "transparent-overlay selected";
        break;
    }

    return(
        <div className="system-category-selector">
            <div className="category-selector-btn">
                <div className="btn-img-container">
                    <img src="https://multimedia.3m.com/mws/media/1441562O/through-penetrations-icon.png" alt="through penetrations icon" />
                </div>
                <div className="btn-text-container">Through penetration</div>
                <div 
                    className={throughPenetrationCss}
                    id="throughPenetrations"
                    onClick={ handleCategorySelectionClick }>
                </div>
            </div>
            <div className="category-selector-btn">
                <div className="btn-img-container">
                    <img src="https://multimedia.3m.com/mws/media/1441563O/joints-icon.png" alt="joints icon" />
                </div>             
                <div className="btn-text-container">Joints</div>
                <div 
                    className={jointsCss}
                    id="joints"
                    onClick={ handleCategorySelectionClick }>
                </div>
            </div>
            <div className="category-selector-btn">
                <div className="btn-img-container">
                    <img src="https://multimedia.3m.com/mws/media/1441564O/wraps-icon.png" alt="wraps icon" />
                </div>
                <div className="btn-text-container">Wraps</div>
                <div 
                    className={wrapsCss}
                    id="wraps"
                    onClick={ handleCategorySelectionClick }>
                </div>
            </div>
            <div className={preloaderCss}>
                <img src="https://multimedia.3m.com/mws/media/1445146O/fireapp-preloader.gif"/>
            </div>
        </div>
    );
}; 

export default SystemCategorySelector; 


