export default function ( state=null, action ) {
    switch( action.type ) {
        case "SYSTEM_CATEGORY_SELECTED":
        return action.payload;

        case "UPDATE_MENU":
        state.map(( menuItem, index ) => {            
            if(menuItem.id === action.payload) {
                menuItem.showPrimaryData = false;
                menuItem.showSecondaryData = false;
                menuItem.showActiveData = true;      
            }
        });
        return [...state]

        case "UPDATE_SECONDARY_MENU": 
        state.map(( menuItem, index ) => {            
            if( menuItem.id === action.payload.currentId ) {
                menuItem.showSecondaryData = true;
                menuItem.showActiveData = true;
            }
        });
        return [...state]

        case "CANCEL_SELECTION":
        state.map(( menuItem, index ) => {            
            if( menuItem.id === action.payload.primaryId ) {
                menuItem.showPrimaryData = true;
            }
        });
        return [...state]

        case "CANCEL_ALL_SELECTIONS":
        state.map(( menuItem, index ) => {            
            menuItem.showPrimaryData = true;
            menuItem.showActiveData = false;
        });
        return [...state]

        default:
        return state;
    }
}


