import React, { Component } from 'react';
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { 
    selectPrimaryMenuItem,
    selectSecondaryMenuItem,
    updateMenu,
    updateSecondaryMenu,
    updateBreadcrumbWithSelection,
    updateResultsWithFilters,
    cancelSelection,
    removeSelectionFromList,
    updateResultsFromCancelation, 
    clearResults,
    cancelAllSelections,
} from "../actions/index";
import SystemCategoryPrimaryMenu from "../components/SystemCategoryPrimaryMenu";
import SystemCategorySecondaryMenu from "../components/SystemCategorySecondaryMenu";
import SelectionList from "../components/SelectionList";
import '../css/refinements.min.css';

class Refinements extends Component {
    constructor( props ) {
        super( props );

        let cancelationId = null;

        this.handlePrimaryMenuSelection = this.handlePrimaryMenuSelection.bind(this);
        this.handleSecondaryMenuSelection = this.handleSecondaryMenuSelection.bind(this);
        this.handleSelectionCancelation = this.handleSelectionCancelation.bind(this);
        this.handleClearAllResults = this.handleClearAllResults.bind(this);
    }

    handleClearAllResults() {
        this.props.clearResults();
        this.props.cancelAllSelections();
    }

    handleSelectionCancelation( primaryId, secondaryId ) {
        const ids = {
            primaryId: primaryId,
            secondaryId: secondaryId
        };

        this.cancelationId = primaryId;
        this.props.cancelSelection( ids );
        this.props.removeSelectionFromList( primaryId );
        this.props.updateResultsFromCancelation(ids);

    }

    handlePrimaryMenuSelection(data, label, id) {
        const primarySelectionObj = {
            data: data,
            label: label,
            id: id
        };
        const secondarySelectionObj = {
            data: this.props.activeSystemCategory,
            currentId: id
        };

        this.cancelationId = null;

        this.props.selectPrimaryMenuItem(primarySelectionObj);
        this.props.updateSecondaryMenu(secondarySelectionObj);
    }

    handleSecondaryMenuSelection(btnLabel, id) {
        const breadcrumbObj = {
            primaryselectionId: this.props.activePrimaryMenuSelection.id,
            primarySelectionLabel: this.props.activePrimaryMenuSelection.label,
            secondarySelectionId: id,
            secondarySelectionLabel: btnLabel
        };

        this.props.updateMenu( this.props.activePrimaryMenuSelection.id );
        this.props.updateBreadcrumbWithSelection( breadcrumbObj );
        this.props.updateResultsWithFilters(breadcrumbObj);
    }

    render(){
        //console.log(this.props);
        let numResults = null;

        if(this.props.resultsData.filteredData) {
            numResults = this.props.resultsData.filteredData ? this.props.resultsData.filteredData.length : "0";
        } else {
            numResults = this.props.resultsData.data ? this.props.resultsData.data.length : "0";
        }
        
        let refinementsCss = this.props.categorySelectedData ? "refinements active" : "refinements"
        let iconUrl = null;
        if(this.props.resultsData.category) {
            switch(this.props.resultsData.category) {
                case "throughPenetrations" :
                iconUrl = "https://multimedia.3m.com/mws/media/1441565O/through-penetration-white.png";
                break;

                case "joints" :
                iconUrl = "https://multimedia.3m.com/mws/media/1441566O/joints-icon-white.png";
                break;

                case "wraps" :
                iconUrl = "https://multimedia.3m.com/mws/media/1441567O/wraps-icon-white.png";
                break;

                default :
                iconUrl = "http://via.placeholder.com/100x100";
                break;
            }
        }

        return( 
            <div className={refinementsCss}>
                <div className="refinements-header" >
                    <div className="results-header">Results: <span>{numResults} Matches</span></div>
                    <div className="result-selection-icon"><img src={iconUrl} alt=""/></div>
                </div>
                <SystemCategoryPrimaryMenu
                    categorySelectedData={ this.props.activeSystemCategory }
                    activePrimaryMenuSelection = { this.props.activePrimaryMenuSelection }
                    passPrimaryMenuData={ this.handlePrimaryMenuSelection }
                    cancelationId={ this.cancelationId }
                />  
                <SystemCategorySecondaryMenu 
                    activeSystemCategory={ this.props.activeSystemCategory }
                    activePrimaryMenuSelection={ this.props.activePrimaryMenuSelection }
                    passSecondaryMenuBtnClickToRefinementContainer = { this.handleSecondaryMenuSelection }
                />
                <SelectionList 
                    selections={this.props.breadcrumbSelections}
                    cancelSelection={this.handleSelectionCancelation}
                    clearAllResults={this.handleClearAllResults}
                />
            </div>    
        );
    }
}

function mapStateToProps(state) {
    //console.log(state);
	return { 
		activeSystemCategory: state.activeSystemCategory,
        activePrimaryMenuSelection: state.activePrimaryMenuSelection,
        breadcrumbSelections: state.breadcrumbSelections
	};
}

function matchDispatchToProps(dispatch) {
	return bindActionCreators({ 
        selectPrimaryMenuItem: selectPrimaryMenuItem,
        selectSecondaryMenuItem: selectSecondaryMenuItem,
        updateMenu: updateMenu,
        updateSecondaryMenu: updateSecondaryMenu,
        updateBreadcrumbWithSelection: updateBreadcrumbWithSelection,
        cancelSelection: cancelSelection,
        removeSelectionFromList: removeSelectionFromList,
        updateResultsWithFilters: updateResultsWithFilters,
        updateResultsFromCancelation: updateResultsFromCancelation,
        clearResults: clearResults,
        cancelAllSelections: cancelAllSelections,
    }, dispatch)
}

export default connect( mapStateToProps, matchDispatchToProps )( Refinements ); 


