import React from 'react';
import PrimaryCategoryMenuBtn from "./PrimaryCategoryMenuBtn";
import '../css/system-category-primary-menu.min.css';

const SystemCategoryPrimaryMenu = ({ 
        categorySelectedData, 
        activePrimaryMenuSelection,
        passPrimaryMenuData,
        cancelationId
    }) => {    

    let mainPrimaryMenuItems = null;
    let otherPrimaryMenuItems = null;
    let css = "primary-category-menu-btn";

    this.handlePrimaryMenuBtnClick = function(data, label, id) {
        passPrimaryMenuData(data, label, id);
    }

    if(activePrimaryMenuSelection) {
        if(cancelationId){
            activePrimaryMenuSelection.id = null;
        } 
    }

    if( categorySelectedData ) {
        mainPrimaryMenuItems = categorySelectedData.map((button, index) => {
            if(activePrimaryMenuSelection) {                
                if(categorySelectedData[index].showPrimaryData) {
                    css = "primary-category-menu-btn";
                    if( activePrimaryMenuSelection.id == button.id && categorySelectedData[index].showActiveData ) {
                        css = "primary-category-menu-btn active";
                    }
                }else {
                    css = "primary-category-menu-btn hide"
                }
            }

            if(button.isPrimary) {
                return( 
                    <PrimaryCategoryMenuBtn 
                        className={ css }
                        key={ button.id }
                        btnLabel={ button.label }
                        data={ button.data }
                        onClick={e => this.handlePrimaryMenuBtnClick(button.data, button.label, button.id)}
                    />    
                );
            }
        });

        otherPrimaryMenuItems = categorySelectedData.map((button, index) => {
            if(activePrimaryMenuSelection) {                
                if(categorySelectedData[index].showPrimaryData) {
                    css = "primary-category-menu-btn";
                    if( activePrimaryMenuSelection.id == button.id && categorySelectedData[index].showActiveData ) {
                        css = "primary-category-menu-btn active";
                    }
                }else {
                    css = "primary-category-menu-btn hide"
                }
            }

            if(!button.isPrimary) {
                return( 
                    <PrimaryCategoryMenuBtn 
                        className={ css }
                        key={ button.id }
                        btnLabel={ button.label }
                        data={ button.data }
                        onClick={e => this.handlePrimaryMenuBtnClick(button.data, button.label, button.id)}
                    />    
                );
            }
        });
    }

    return( 
        <div className="system-category-primary-menu-container" >
            <div className="primary-category-header">
                <span>Refine results</span>
                <img src="https://multimedia.3m.com/mws/media/1441568O/refinements-icon.png" alt="refinement icon"/>
            </div>
            <div className="main-options"> 
                <div className="options-header">Main options</div>
                { mainPrimaryMenuItems } 
            </div>
            <div className="other-options">
                <div className="options-header">Other options</div>
                { otherPrimaryMenuItems } 
            </div>
            
        </div>    
    );
}; 

export default SystemCategoryPrimaryMenu;  


